import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { SignFormGroup } from "../../../forms/sign.form";

import { Storage } from "../../../shared/utils/storage";
import { Otp } from "../../../entities/otp.entity";
import { Account } from "./../../../entities/account.entity";
import { RestService } from "../../../common/rest.service";
import { ModalPopupComponent } from "./modal.popup.component";
import { MdDialog, MdDialogRef } from "@angular/material";
import { Prop } from "../../../common/props";

@Component({
  selector: "app-account-business",
  templateUrl: "./account.bussiness.component.html"
})
export class AccountBusinessComponent implements OnInit {
  myForm: any;
  isBankThere: boolean = false;
  @Input("account")
  account: Account = new Account();

  @Output("businessEvent")
  businessEvent = new EventEmitter<string>();

  @Output("backEvent")
  backEvent = new EventEmitter<string>();

  constructor(
    private appService: AppService,
    private restService: RestService,
    private dialog: MdDialog
  ) {
    this.isBankThere = false;
    this.myForm = SignFormGroup.AccountBusinessFormGroup();
  }
  ngOnInit() {
    console.log("Account Bussiness");
  }

  next() {
    this.businessEvent.emit();
  }
  // openDialog() {
  //   const dialogRef = this.dialog.open(ModalPopupComponent);
  //   dialogRef.afterClosed().subscribe(result => {
  //       if(result){
  //         if(this.myForm.valid){
  //           this.businessEvent.emit();
  //         } else{
  //           this.appService.showMessage("Please enter form details.")
  //         }

  //       } else {
  //         this.appService.showMessage("Registration Successfull")
  //       }
  //   });
  // }

  back() {
    this.backEvent.emit();
  }
  focusOutFunction() {
    console.log("Here");

    console.log(this.account.bankDetailsPayload.ifscCode);
    this.restService
      .bankDetails(this.account.bankDetailsPayload.ifscCode)
      .subscribe(result => {
        if (result) {
          console.log(result);
          this.isBankThere = true;
          this.account.bankDetailsPayload.bankName = result.bankName;
          this.account.bankDetailsPayload.bankAddress = result.address;
          this.account.bankDetailsPayload.branch = result.branch;
        } else {
          this.isBankThere = false;
        }
      });
  }
}



