import { AccountLicenseComponent } from './account/account.license.component';
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CommonModule } from "@angular/common";
import { FlexLayoutModule } from "@angular/flex-layout";

import { SharedModule } from "../../shared/shared.module";
import { SigninComponent } from "./signin/signin.component";
import { ForgotComponent } from "./forgetpassword/forgotpassword.component";
import { OtpComponent } from "./otp/otp.component";
import { NewPasswordComponent } from "./newpassword/newpassword.component";
import { SignupComponent } from "./signup/signup.component";
import { TermsPopupComponent } from "./signup/terms.popup.component";
import { OtpPopupComponent } from "./signup/otp.popup.component";
import { ModalPopupComponent } from "./account/modal.popup.component";

import { AccountComponent } from "./account/account.component";
import { AccountProfileComponent } from "./account/account.profile.component";
import { AccountAddressComponent } from "./account/account.address.component";
import { AccountBusinessComponent } from "./account/account.bussiness.component";
import { AccountRetailComponent } from './account/account.retail.component';
//import { NgProgressCustomBrowserXhr } from 'ngx-progressbar';

const routes: Routes = [
  { path: "", component: SigninComponent },
  { path: "signin", component: SigninComponent },
  { path: "forgotpassword", component: ForgotComponent },
  { path: "otp", component: OtpComponent },
  { path: "newpassword", component: NewPasswordComponent },
  { path: "signup", component: SignupComponent },
  { path: "termspopup", component: TermsPopupComponent },
  { path: "modalpopup", component: ModalPopupComponent },
  { path: "otppopup", component: OtpPopupComponent },
  { path: "account", component: AccountComponent },
  { path: "account-retail", component:AccountRetailComponent},
  { path: "license",component:AccountLicenseComponent}
];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FlexLayoutModule,
    RouterModule.forChild(routes)
  ],
  declarations: [
    SigninComponent,
    ForgotComponent,
    OtpComponent,
    NewPasswordComponent,
    SignupComponent,
    TermsPopupComponent,
    OtpPopupComponent,
    ModalPopupComponent,
    AccountComponent,
    AccountProfileComponent,
    AccountAddressComponent,
    AccountBusinessComponent,
    AccountLicenseComponent,
    AccountRetailComponent
  ],
  exports: [CommonModule]
})
export class SignModule {}



