import { Component, OnInit } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { RestService } from "../../../common/rest.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { SignFormGroup } from "../../../forms/sign.form";

import { Storage } from "../../../shared/utils/storage";
import { Otp } from "../../../entities/otp.entity";
import { Account } from "./../../../entities/account.entity";
import { IStepChangeEvent } from "@covalent/core";
import { MdDialog, MdDialogConfig } from "@angular/material";
import { ModalPopupComponent } from "./modal.popup.component";

@Component({
  selector: "app-account",
  templateUrl: "./account.component.html"
})
export class AccountComponent implements OnInit {
  myForm: any;
  account: Account;
  currentStep: number = 1;
  step1: any;
  step2: any;
  step3: any;
  dialogConfig = new MdDialogConfig();

  constructor(
    private dialog: MdDialog,
    private appService: AppService,
    private restService: RestService
  ) {
    //const dialogRef = this.dialog.open(ModalPopupComponent);

    this.account = new Account();
    this.dialogConfig.panelClass = "signup-dialog";
  }

  ngOnInit() {
    setTimeout(() => {
      this.changeStep();
      let id: any = this.appService.getParam("id");
      if (id) {
        this.profileDetails(id);
      } else {
        this.appService.navigate("/", null);
      }
    }, 100);
  }
  profileDetails(id: any) {
    this.restService.profileDetails({ id: id }).subscribe(result => {
      console.log(result);
      if (result) {
        this.account.email = result.email;
        this.account.mobile = result.mobile;
        this.account.role = "user";
        this.account.id = result.id;
      } else {
        this.appService.navigate("/", null);
      }
    });
  }
  changeEvent(event1: any, event2: any, event3: any) {
    this.step1 = event1;
    this.step2 = event2;
    this.step3 = event3;
  }
  changeStep() {
    this.step1.disabled = true;
    this.step2.disabled = true;
    this.step3.disabled = true;

    this.step1.active = false;
    this.step2.active = false;
    this.step3.active = false;

    let activeStep: any = this.getActiveStep();

    activeStep.disabled = false;
    activeStep.active = true;
  }

  getActiveStep() {
    switch (this.currentStep) {
      case 1:
        return this.step1;
      case 2:
        return this.step2;
      case 3:
        return this.step3;
      default:
        return this.step1;
    }
  }
  //   signup(){
  //     this.appService.navigate('/signup',[]);
  //   }

  profileEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 2;
    this.changeStep();
  }
  addressEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 3;
    this.changeStep();
  }
  businessEvent($event) {
    console.log($event);
    let reqData: any = {};
    reqData.profile = this.account.profile;

    reqData.address = this.account.address;
    //delete reqData.address.area;

    reqData.company = this.account.company;
    reqData.bankDetailsPayload = this.account.bankDetailsPayload;
    //delete reqData.company.website;
    //delete reqData.company.currency;

    reqData.account = {};
    reqData.account.email = this.account.email;
    reqData.account.mobile = this.account.mobile;
    reqData.account.role = this.account.role;
    reqData.account.id = this.account.id;
    // const dialogRef = this.dialog.open(ModalPopupComponent);
    this.restService.profileUpdate(reqData).subscribe(result => {
      console.log(result);
      if (result) {
        this.appService.navigate("/sign/license",{userId:this.account.id,mobile:this.account.mobile,email:this.account.email});
        
        // this.dialogConfig.data = {
        //   title: "Registration successful !"
        // };

        // const dialogRef = this.dialog.open(
        //   ModalPopupComponent,
        //   this.dialogConfig
        // );
        // dialogRef.afterClosed().subscribe(result => {
        //   this.appService.navigate("/sign/signin", null);
        // });
      }
    });
  }
  backEvent() {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "none";
    this.currentStep = this.currentStep - 1;
    this.changeStep();
  }

  // activeEvent(): void {
  //   this.stepActive()
  // }
}



