import { SignFormGroup } from "./../../../forms/sign.form";
import { Account } from "./../../../entities/account.entity";
import { MdDialog } from "@angular/material";
import { AppService } from "./../../../shared/service/app.service";
import { MdDialogConfig } from "@angular/material";
import { Component, OnInit } from "@angular/core";
import { RestService } from "app/common/rest.service";

@Component({
  selector: "app-account-retail",
  templateUrl: "./account.retail.component.html"
})
export class AccountRetailComponent implements OnInit {
  myForm: any;
  account: Account;
  dialogConfig = new MdDialogConfig();
  stateList: any[] = [];
  stateListTemp: any[] = [];
  filteredStates: any[] = [];

  constructor(
    private dialog: MdDialog,
    private appService: AppService,
    private restService: RestService
  ) {
    this.myForm = SignFormGroup.AccountRetailFormGroup();
    this.account = new Account();
    this.dialogConfig.panelClass = "signup-dialog";
  }

  ngOnInit() {
    setTimeout(() => {
      let id: any = this.appService.getParam("id");
      if (id) {
        //    alert(id)
        this.accountDetails(id);
        this.stateLoad();
      } else {
        this.appService.navigate("/", null);
      }
    }, 100);
  }

  accountDetails(id: any) {
    this.restService.accountDetails({ id: id }).subscribe(result => {
      console.log("account details --" + result);
      if (result) {
        this.account.email = result.email;
        this.account.mobile = result.mobile;
        this.account.role = "user";
        this.account.id = result.id;
      } else {
        alert("stoped in profile details ");
        this.appService.navigate("/", null);
      }
    });
  }

  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s.key.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }

  selectedAddress(item: any) {
    if (item) {
      this.account.address.state = item[0];
    }
  }

  stateLoad() {
    //this.restService.dataloadFromFile()
    this.restService
      .dataloadFromFile("./assets/json/states.json")
      .subscribe(results => {
        this.stateList = results["states"];
        console.log(this.stateList);
      });
  }
  focusOutFunction() {
    let array: any = [];
    this.stateList.forEach(item => {
      if (this.account.address.state == item[0]) {
        array.push(item[0]);
      }
    });
    if (array.length == 0) {
      // this.appService.showMessage('Please Select Correct State Value')
      this.account.address.state = null;
    }
  }

  updateProfile() {
    // alert('update profile reading to submit')
    let reqData: any = {};
    reqData.profile = this.account.profile;

    reqData.address = this.account.address;

    reqData.company = this.account.company;
    reqData.company.type = "Retail";
    reqData.bankDetailsPayload = this.account.bankDetailsPayload;

    reqData.account = {};
    // reqData.account.email = this.account.email;
    reqData.account.mobile = this.account.mobile;
    reqData.account.role = this.account.role;
    reqData.account.id = this.account.id;
    this.restService.profileUpdate(reqData).subscribe(result => {
      console.log(result);
      if (result) {
        this.appService.navigate("/sign/license", {
          userId: this.account.id,
          mobile: this.account.mobile,
          email: this.account.email
        });
      }
    });
  }
}



