import { HttpService } from './../../../shared/service/http.service';
import { ModalPopupComponent } from './modal.popup.component';
import { MdDialog, MdDialogConfig } from '@angular/material';
import { Component, AfterContentInit, Input, Output, EventEmitter, OnInit, state } from '@angular/core';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from 'app/common/rest.service';

@Component({
  selector: 'app-account-license',
  templateUrl: './account.license.component.html'

})
export class AccountLicenseComponent implements OnInit {
  userAccountId: string;
  macAddress: string;
  mobile: string;
  email: string;
  showRetry: boolean = false;
  showSpinner: boolean = true;
  dialogConfig = new MdDialogConfig();
  constructor(private dialog: MdDialog,
    private appService: AppService, private restService: RestService) {
  }

  ngOnInit() {
    this.userAccountId = this.appService.getParam("userId");
    this.mobile = this.appService.getParam("mobile");
    this.email = this.appService.getParam("email");
    //this.getMacAddress();
    setTimeout(() => {
      this.generateLicense();
    }, 1000);
  }

  onRetry() {    
    this.generateLicense();
  }

  onFailed(){
    this.showSpinner = false;
    this.showRetry = true;
  }
  onSuccess(){
    this.showSpinner = false;
    this.showRetry = false;
  }

  generateLicense() {

    this.restService.getMacAddress(null).subscribe(result => {
      console.log(result);
      if (result) {
        this.macAddress = result.macAddress;
        if (this.macAddress != null && this.macAddress != "") {
          this.restService.generateLicense({ mac: this.macAddress, mobile: this.mobile, email: this.email })
            .subscribe(result => {
              console.log("DSFDSFDSFDSFDF"+result);
              if (result) {
                let licenseKey = result.licenseKey;
                this.restService.saveLicense({ userAccountId: this.userAccountId, licenseKey: licenseKey })
                  .subscribe(result => {
                    console.log(result);
                    if (result) {
                      this.dialogConfig.data = {
                        title: "Registration successful !"
                      };

                      const dialogRef = this.dialog.open(
                        ModalPopupComponent,
                        this.dialogConfig
                      );
                      dialogRef.afterClosed().subscribe(result => {
                        this.appService.navigate("/sign/signin", null);
                      });
                    }
                    else{
                      this.onFailed();
                    }
                  });
              }
              else{
                this.onFailed();
              }
            });
        }
        else {
          this.onFailed();
        }
      }
    });
  }
}


