import { Component, OnInit } from "@angular/core";
import { AppService } from "./../../../shared/service/app.service";
import { AppFormGroup } from "../../../shared/common/app.form";
import { Forgot } from "./../../../entities/forgotpassword.entity";
import { SignFormGroup } from "../../../forms/sign.form";
import { ReqBody } from "../../../shared/common/app.entity";
import { RestService } from "../../../common/rest.service";
import { Otp } from "./../../../entities/otp.entity";
import { MdDialog, MdDialogConfig } from "@angular/material";
import { ModalPopupComponent } from "./../account/modal.popup.component";

@Component({
  selector: "app-forgotpassword",
  templateUrl: "./forgotpassword.component.html"
})
export class ForgotComponent implements OnInit {
  myForm: any;
  forgotpassword: Forgot;
  condition: string;
  otp: Otp;
  showorhide: string;
  isVisible: string;
  isResetPassword: boolean = false;
  dialogConfig = new MdDialogConfig();

  constructor(
    private dialog: MdDialog,
    private appService: AppService,
    private restService: RestService
  ) {
    this.myForm = SignFormGroup.ForgotFormGroup();
    this.forgotpassword = new Forgot();
    this.isResetPassword = false;
    this.condition = "mobile";
    this.showorhide = "password";
    this.isVisible = "show";
    this.dialogConfig.panelClass = "signup-dialog";
  }
  ngOnInit() {}
  showPassword() {
    this.showorhide = "password";
    this.isVisible = "show";
  }
  hidePassword() {
    this.showorhide = "text";
    this.isVisible = "hide";
  }
  otpInit() {
    this.condition = "change";
    // if (this.forgotpassword) {
    //   this.otp = new Otp();
    //   this.otp.type = 'forgot';
    //   if (!isNaN(this.forgotpassword.mobile)) {
    //     this.otp.mobile = this.forgotpassword.mobile;
    //     this.otp.email = '';
    //   } else {
    //     this.otp.mobile = '';
    //     this.otp.email = this.forgotpassword.mobile;
    //   }

    //   this
    //     .restService
    //     .otpCreate(this.otp)
    //     .subscribe(result => {
    //       if (result) {
    //         this
    //           .appService
    //           .showMessage(result.message);
    //         this.condition = 'otp'
    //       }
    //     });
    // }
  }
  otpSend() {
    this.condition = "change";
  }
  otpResend() {
    if (this.forgotpassword) {
      this.otp = new Otp();
      this.otp.type = "forgot";
      if (!isNaN(this.forgotpassword.mobile)) {
        this.otp.mobile = this.forgotpassword.mobile;
        this.otp.email = "";
      } else {
        this.otp.mobile = "";
        this.otp.email = this.forgotpassword.mobile;
      }
      this.restService.otpResend(this.otp).subscribe(result => {
        if (result) {
          this.appService.showMessage(result.message);
        }
      });
    }
  }
  changePassword() {
    let reqData: any = {};
    if (!isNaN(this.forgotpassword.mobile)) {
      reqData.mobile = this.forgotpassword.mobile;
      reqData.email = null;
    } else {
      reqData.mobile = null;
      reqData.email = this.forgotpassword.mobile;
    }
    reqData.password = this.forgotpassword.password;
    this.restService.changePassword(reqData).subscribe(result => {
      if (result) {
        // this.appService.showMessage(result.message);

        this.dialogConfig.data = {
          title: "The password is successfully reset. Sign in"
        };

        const dialogRef = this.dialog.open(
          ModalPopupComponent,
          this.dialogConfig
        );
        dialogRef.afterClosed().subscribe(result => {
          this.appService.navigate("/sign/signin", []);
        });
        // this.appService.navigate("/sign/signin", []);
      }
    });
  }

  login() {
    this.appService.navigate("/sign/signin", []);
  }
  otpVerify() {
    if (!this.forgotpassword.otp) {
      this.appService.showMessage("Otp Code is required");
      return;
    } else {
      this.otp = new Otp();
      this.otp.type = "forgot";
      if (!isNaN(this.forgotpassword.mobile)) {
        this.otp.mobile = this.forgotpassword.mobile;
        this.otp.email = "";
      } else {
        this.otp.mobile = "";
        this.otp.email = this.forgotpassword.mobile;
      }
      this.otp.otpCode = this.forgotpassword.otp;
      this.restService.otpVerify(this.otp).subscribe(result => {
        if (result) {
          this.appService.showMessage(result.message);
          this.condition = "change";
        }
      });
    }
  }
  //   save(){     this.restService     .signup(this.signup)
  // .subscribe((result: any) => {       if (result) {
  // this.appService.showMessage(result.message);         this.login();       }
  //  }); }
}



