import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";
export class SignFormGroup extends AppFormGroup {
  static SigninFormGroup() {
    return new FormBuilder().group({
      signin_userId: [
        "",
        Validators.compose([Validators.required, Validators.maxLength(99)])
      ],
      signin_password: [
        "",
        Validators.compose([Validators.required, Validators.maxLength(99)])
      ]
    });
  }

  /*-----signup----*/
  static SignUpFormGroup() {
    return new FormBuilder().group({
      signup_mobile: [
        "",
        Validators.compose([
          Validators.pattern(this.MOBILE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ],
      signup_email: [
        "",
        Validators.compose([
          Validators.pattern(this.SIGN_UP_EMAIL_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      signup_password: [
        "",
        Validators.compose([
          Validators.required,
          Validators.pattern(this.DATA_PATTERN),
          Validators.maxLength(99)
        ])
      ]
    });
  }
  /*----forgot password--*/
  static ForgotFormGroup() {
    return new FormBuilder().group({
      forgotpassword_mobile_conformmobile: [
        "",
        Validators.compose([
          Validators.pattern(this.MOBILE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ],
      // forgotpassword_mobile_email                             : ['', Validators.compose([ Validators.pattern(this.DATA_PATTERN), Validators.maxLength(99)]) ],
      forgotpassword_otp_otp: [
        "",
        Validators.compose([
          Validators.pattern(this.NUMBER_PATTERN),
          Validators.maxLength(9)
        ])
      ],
      forgotpassword_change_mobile: [
        "",
        Validators.compose([
          Validators.pattern(this.MOBILE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ],
      forgotpassword_change_password: [
        "",
        Validators.compose([
          Validators.pattern(this.DATA_PATTERN),
          Validators.maxLength(99)
        ])
      ]
    });
  }

  static OtpFormGroup() {
    return new FormBuilder().group({
      otp_email: [
        "",
        Validators.compose([
          Validators.pattern(this.EMAIL_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      otp_mobile: [
        "",
        Validators.compose([
          Validators.pattern(this.MOBILE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ],
      otp_otp: [
        "",
        Validators.compose([
          Validators.pattern(this.NUMBER_PATTERN),
          Validators.maxLength(9)
        ])
      ]
      // forgotpassword_newpassword 		                   : ['', Validators.compose([Validators.pattern(this.DATA_PATTERN), Validators.maxLength(99)]) ],
    });
  }

  static NewPasswordFormGroup() {
    return new FormBuilder().group({
      newpassword_email: [
        "",
        Validators.compose([
          Validators.pattern(this.EMAIL_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      newpassword_mobile: [
        "",
        Validators.compose([
          Validators.pattern(this.MOBILE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ],
      newpassword_password: [
        "",
        Validators.compose([
          Validators.required,
          Validators.pattern(this.DATA_PATTERN),
          Validators.maxLength(99)
        ])
      ]
      // forgotpassword_otp               :['', Validators.compose([Validators.pattern(this.NUMBER_PATTERN), Validators.maxLength(4)]) ],
      // forgotpassword_newpassword 		                   : ['', Validators.compose([Validators.pattern(this.DATA_PATTERN), Validators.maxLength(99)]) ],
    });
  }

  static AccontProfileFormGroup() {
    return new FormBuilder().group({
      account_profile_firstName: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_profile_lastName: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_name: [
        "",
        Validators.compose([
          Validators.pattern(this.BUSI_NAME_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_profile_email: [
        "",
        Validators.compose([
          Validators.pattern(this.SIGN_UP_EMAIL_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_website: [
        "",
        Validators.compose([
          Validators.pattern(this.WEBSITE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_tan: [
        "",
        Validators.compose([
          Validators.pattern(this.PAN_NO_PATTERN),
          Validators.maxLength(11)
        ])
      ],
      account_profile_phone: [
        "",
        Validators.compose([
          Validators.pattern(this.PHONE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ]
    });
  }

  static AccountAddressFormGroup() {
    return new FormBuilder().group({
      account_address_lane: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_city: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_area: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_state: [
        "",
        Validators.compose([
          Validators.pattern(this.DATA_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_street: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_zipCode: [
        "",
        Validators.compose([
          Validators.pattern(this.PIN_CODE_PATTERN),
          Validators.maxLength(8)
        ])
      ]
    });
  }
  static AccountBusinessFormGroup() {
    return new FormBuilder().group({
      account_company_currency: [
        "",
        Validators.compose([
          Validators.pattern(this.DATA_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_type: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_gstin: [
        "",
        Validators.compose([
          Validators.pattern(this.GST_CODE_PATTERN),
          Validators.maxLength(45)
        ])
      ],
      account_bank_accno: ["", Validators.compose([Validators.maxLength(99)])],
      account_bank_ifscCode: [
        "",
        Validators.compose([Validators.maxLength(99)])
      ]
    });
  }

  static AccountRetailFormGroup() {
    return new FormBuilder().group({
      account_profile_firstName: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_profile_lastName: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_name: [
        "",
        Validators.compose([
          Validators.pattern(this.BUSI_NAME_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_profile_email: [
        "",
        Validators.compose([
          Validators.pattern(this.SIGN_UP_EMAIL_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_company_website: [
        "",
        Validators.compose([
          Validators.pattern(this.WEBSITE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      // account_company_tan: [
      //   "",
      //   Validators.compose([
      //     Validators.pattern(this.PAN_NO_PATTERN),
      //     Validators.maxLength(11)
      //   ])
      // ],
      account_profile_phone: [
        "",
        Validators.compose([
          Validators.pattern(this.PHONE_NO_PATTERN),
          Validators.maxLength(10)
        ])
      ],
      account_address_lane: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_city: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_area: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_state: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_street: [
        "",
        Validators.compose([
          Validators.pattern(this.ALFA_NUM_DES_SPACE_PATTERN),
          Validators.maxLength(99)
        ])
      ],
      account_address_zipCode: [
        "",
        Validators.compose([
          Validators.pattern(this.PIN_CODE_PATTERN),
          Validators.maxLength(8)
        ])
      ]
    });
  }
}



