import { Component, OnInit } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import fscreen from "fscreen";

import { AppFormGroup } from "./../../../shared/common/app.form";
import { Storage } from "../../../shared/utils/storage";
import { RestService } from "../../../common/rest.service";
import { Signin } from "../../../entities/signin.entity";
import { SignFormGroup } from "../../../forms/sign.form";
import { MdProgressSpinnerModule } from "@angular/material";
import { ApexService } from "../../../shared/service/apex.service";
import { Prop } from "../../../common/props";

@Component({ selector: "app-signin", templateUrl: "./signin.component.html" })
export class SigninComponent implements OnInit {
  myForm: any;
  signin: Signin;
  showorhide: string;
  isVisible: string;
  _loaderSubscription: any;
  showSpinner: Boolean = false;
  elem: any;
  constructor(
    private appService: AppService,
    private apexService: ApexService,
    private restService: RestService
  ) {
    this.myForm = SignFormGroup.SigninFormGroup();
    this.signin = new Signin();
    Storage.clearSession();
    this.showorhide = "password";
    this.isVisible = "show";
    this._loaderSubscription = this.apexService.loaderEvent.subscribe(data => {
      this.showSpinner = data;
    });
  }
  ngOnInit() {
    this.elem = document.documentElement;
    Storage.clearSession();
    this.appService.sessionUserEmit(null);
  }

  // openFullScreen() {
  //   fscreen.fullscreenEnabled = true;
  //   if (fscreen.fullscreenEnabled) {
  //     fscreen.addEventListener("fullscreenchange", handler, false);
  //     fscreen.requestFullscreen(this.elem);
  //   }

  //   function handler() {
  //     if (fscreen.fullscreenElement !== null) {
  //       console.log("Entered fullscreen mode");
  //     } else {
  //       console.log("Exited fullscreen mode");
  //     }
  //   }
  // }

  keyDownFunction(event) {
    if (event.keyCode == 13) {
      alert("you just clicked enter");
      // rest of your code
    }
  }
  showPassword() {
    this.showorhide = "password";
    this.isVisible = "show";
  }
  hidePassword() {
    this.showorhide = "text";
    this.isVisible = "hide";
  }
  connect($event): void {
    // this.openFullScreen();
    this.restService.showSpinner(true);
    this.restService.signin(this.signin).subscribe(data => {
      if (data) {
        console.log(data);
        if (data.account.regStatus == 0) {
          this.appService.navigate(Prop.ACCOUNT_INIT_PAGE, [
            { id: data.account.id }
          ]);
        }
        if (data.account.regStatus == 1) {
          this.appService.navigate("/sign/license", {
            userId: data.account.id,
            mobile: data.account.mobile,
            email: data.account.email
          });
        } else {
          this.restService
            .validateLicense({
              userAccountId: data.account.id,
              licenseKey: data.account.licenseKey
                ? data.account.licenseKey
                : "invalid"
            })
            .subscribe(license => {
              console.log(license);
              //console.log("1----");
              this.restService.showSpinner(false);
              if (license) {
                this.restService.setSession(data);
                //console.log("2----");
                this.restService.showSpinner(false);
                this.appService.navigate("/admin/dashboard", null);
              }
            });
        }
      }
    });
  }
  signup() {
    this.appService.navigate("/sign/signup", []);
  }
  forgotPassword() {
    this.appService.navigate("/sign/forgotpassword", []);
  }
  landingpage() {
    this.appService.navigate("landingpage", []);
  }
}



