import { Component, AfterContentInit , Input, Output, EventEmitter } from '@angular/core';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { SignFormGroup } from '../../../forms/sign.form';

import { Storage} from '../../../shared/utils/storage';
import { Otp } from '../../../entities/otp.entity';
import { Account } from './../../../entities/account.entity';


@Component({
  selector: 'app-account-profile',
  templateUrl: './account.profile.component.html'
 
})
export class AccountProfileComponent implements AfterContentInit {
          myForm: any;

          @Input('account')
          account : Account;

          @Output('profileEvent')
          profileEvent = new EventEmitter<string>();

        constructor(private appService: AppService) {
            this.myForm = SignFormGroup.AccontProfileFormGroup();

        }
                
        ngAfterContentInit(){
            console.log("Account Profile");
        }

        next() {
            this.profileEvent.emit()
        }
 
}



