import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { SignFormGroup } from "../../../forms/sign.form";
import { RestService } from "./../../../common/rest.service";
import { Storage } from "../../../shared/utils/storage";
import { Otp } from "../../../entities/otp.entity";
import { Account } from "./../../../entities/account.entity";
import { Address } from "../../../entities/address.entity";

@Component({
  selector: "app-account-address",
  templateUrl: "./account.address.component.html"
})
export class AccountAddressComponent implements OnInit {
  myForm: any;

  @Input()
  filteredStates: any[] = [];
  @Input()
  address: Address[] = [];
  @Output()
  outputEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Input()
  inputData: any;

  @Input("account")
  account: Account;

  @Output("addressEvent")
  addressEvent = new EventEmitter<string>();

  @Output("backEvent")
  backEvent = new EventEmitter<string>();
  // stateList1: Array<string> = new Array<string>();
  stateList: any[] = [];
  stateListTemp: any[] = [];
  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    this.account = new Account();
    this.account.address.state = "";
    this.myForm = SignFormGroup.AccountAddressFormGroup();
  }

  ngOnInit() {
    console.log("Account Address");
    this.stateLoad();
  }

  next() {
    this.addressEvent.emit();
  }
  back() {
    this.backEvent.emit();
  }
  //   signup(){
  //     this.appService.navigate('/signup',[]);
  //   }

  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s[0].toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }

  selectedAddress(item: any) {
    if (item) {
      this.account.address.state = item[0];
    }
  }

  stateLoad() {
    this.restService.dataload("State").subscribe(results => {
      this.stateList = results;
    });
  }
  focusOutFunction(){
    let array:any =[]
    this.stateList.forEach(item => {
  if(this.account.address.state == item[0]){
        array.push(item[0])
    }
    });
    if(array.length == 0){
      // this.appService.showMessage('Please Select Correct State Value')
      this.account.address.state = null;
    }
  }
}



