import { Component, OnInit } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { RestService } from "../../..//common/rest.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { SignFormGroup } from "../../../forms/sign.form";
import { MdDialog, MdDialogRef } from "@angular/material";
import { Storage } from "../../../shared/utils/storage";
import { Otp } from "../../../entities/otp.entity";
import { Signup } from "../../../entities/signup.entity";

@Component({
  selector: "app-otppopup",
  templateUrl: "./otp.popup.component.html"
})
export class OtpPopupComponent implements OnInit {
  myForm: any;
  otp: Otp;
  signup: Signup;

  constructor(
    private appService: AppService,
    private restService: RestService,
    private mdDialogRef: MdDialogRef<any>
  ) {
    this.myForm = SignFormGroup.OtpFormGroup();
    this.otp = new Otp();
    this.otp.type = "register";
  }

  ngOnInit() {}

  account() {
    this.appService.navigate("/sign/account", []);
  }

  otpResend() {
    if (this.signup) {
      this.otp.mobile = this.signup.mobile;
      this.otp.email = this.signup.email;
      this.restService.otpResend(this.otp).subscribe(result => {
        if (result) {
          this.appService.showMessage(result.message);
        }
      });
    }
  }
  otpVerify() {
    if (!this.otp.otpCode) {
      this.appService.showMessage("Otp Code is required");
      return;
    } else {
      this.otp.mobile = this.signup.mobile;
      this.otp.email = this.signup.email;
      this.restService.otpVerify(this.otp).subscribe(result => {
        if (result) {
          this.mdDialogRef.close(result);
          this.appService.showMessage(result.message);
        }
      });
    }
  }
  close() {
    this.mdDialogRef.close(false);
  }
}



