import { Component, OnInit } from "@angular/core";
import { MdDialog, MdDialogRef } from "@angular/material";
import { AppService } from "./../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
// import { LoginUser } from '../../shared/entities/loginUser.entity';
import { Storage } from "../../../shared/utils/storage";
import { Signup } from "../.././../entities/signup.entity";
import { RestService } from "../../../common/rest.service";
import { SignFormGroup } from "../../../forms/sign.form";
import { TermsPopupComponent } from "./terms.popup.component";
import { OtpPopupComponent } from "./otp.popup.component";
import { Prop } from "../../../common/props";
@Component({
  selector: "app-signup",
  templateUrl: "./signup.component.html"
})
export class SignupComponent implements OnInit {
  myForm: any;
  signup: Signup;
  showorhide: string;
  isVisible: string;
  //   private loginUser: LoginUser;

  constructor(
    private appService: AppService,
    private restService: RestService,
    private dialog: MdDialog
  ) {
    this.myForm = SignFormGroup.SignUpFormGroup();
    //   this.loginUser = new LoginUser();
    this.signup = new Signup();
    this.showorhide = "password";
    this.isVisible = "show";
  }
  ngOnInit() {
    Storage.clearSession();
    this.appService.sessionUserEmit(null);
  }
  showPassword() {
    this.showorhide = "password";
    this.isVisible = "show";
  }
  hidePassword() {
    this.showorhide = "text";
    this.isVisible = "hide";
  }
  openDialog() {
    const dialogRef = this.dialog.open(TermsPopupComponent);
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        if (this.myForm.valid) {
          this.otpInit();
        } else {
          this.appService.showMessage("Please enter form details.");
        }
      } else {
        this.appService.showMessage(
          "Please agree terms and conditions for proceed."
        );
      }
    });
  }
  openDialogOtp() {
    console.log(this.signup);
    const dialogRef = this.dialog.open(OtpPopupComponent);
    dialogRef.componentInstance.signup = this.signup;
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.restService.signup(this.signup).subscribe(result => {
          this.appService.navigate(Prop.ACCOUNT_INIT_PAGE, [{ id: result.id }]);
        });
      } else {
        //this.appService.showMessage("Please agree terms and conditions for proceed.")
      }
    });
  }
  login() {
    this.appService.navigate("/sign/signin", []);
  }
  save() {
    this.restService.signup(this.signup).subscribe((result: any) => {
      if (result) {
        this.appService.showMessage(result.message);
        this.login();
      }
    });
  }
  otp: any = {};
  otpInit() {
    // if(this.signup){
    //    this.otp.type= 'register';
    //   this.otp.mobile = this.signup.mobile;
    //   this.otp.email = this.signup.email;
    //   this.restService.otpCreate(this.otp)
    //     .subscribe( result => {
    //       if(result){
    //         this.openDialogOtp();
    //         this.appService.showMessage(result.message);
    //       }

    //     });
    // }
    this.restService.signup(this.signup).subscribe(result => {
      this.appService.navigate(Prop.ACCOUNT_INIT_PAGE, [{ id: result.id }]);
    });
  }

  cancel() {
    this.appService.navigate("/landingpage", []);
  }
}



